<template>
  <view class="rider-nav">
    <view class="nav-item" :class="{ active: currentPage === 'hall' }" @tap="switchPage('hall')">
      <text class="nav-icon">üè†</text>
      <text class="nav-text">Êé•ÂçïÂ§ßÂéÖ</text>
    </view>
    <view class="nav-item" :class="{ active: currentPage === 'running' }" @tap="switchPage('running')">
      <text class="nav-icon">üì¶</text>
      <text class="nav-text">ÈÖçÈÄÅ‰∏≠</text>
    </view>
    <view class="nav-item" :class="{ active: currentPage === 'stats' }" @tap="switchPage('stats')">
      <text class="nav-icon">üìä</text>
      <text class="nav-text">‰∏öÁª©</text>
    </view>
    <view class="nav-item switch-user-item" @tap="switchToUser">
      <text class="nav-icon">üë§</text>
      <text class="nav-text">Áî®Êà∑Ê®°Âºè</text>
    </view>
  </view>
</template>

<script>
import { switchMode } from '@/api/user.js';
import { setUserInfo } from '@/utils/token.js';

export default {
  props: {
    currentPage: {
      type: String,
      default: 'hall'
    }
  },

  methods: {
    switchPage(page) {
      if (page === this.currentPage) return;

      uni.redirectTo({
        url: `/pages/${page}/${page}`
      });
    },

    // ÂàáÊç¢‰∏∫Áî®Êà∑Ê®°Âºè
    async switchToUser() {
      try {
        uni.showModal({
          title: 'ÂàáÊç¢Ë∫´‰ªΩ',
          content: 'Á°ÆÂÆöÂàáÊç¢‰∏∫Áî®Êà∑Ê®°ÂºèÂêóÔºü',
          success: async (res) => {
            if (res.confirm) {
              uni.showLoading({ title: 'ÂàáÊç¢‰∏≠...', mask: true });

              const result = await switchMode({ targetMode: 1 }); // 1-Áî®Êà∑Ê®°Âºè

              uni.hideLoading();

              // Êõ¥Êñ∞Êú¨Âú∞Áî®Êà∑‰ø°ÊÅØ
              if (result.data) {
                setUserInfo(result.data);
              }

              uni.showToast({
                title: 'ÂàáÊç¢ÊàêÂäü',
                icon: 'success'
              });

              // Ë∑≥ËΩ¨Âà∞Áî®Êà∑È¶ñÈ°µ
              setTimeout(() => {
                uni.switchTab({
                  url: '/pages/index/index'
                });
              }, 1000);
            }
          }
        });
      } catch (error) {
        uni.hideLoading();
        console.error('ÂàáÊç¢Â§±Ë¥•:', error);
        uni.showToast({
          title: 'ÂàáÊç¢Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      }
    }
  }
};
</script>

<style scoped>
